:root{
  --color1: #12c2e9;
  --color2: #c471ed;
  --color3: #f64f59;
}
.circle-background {
  fill: none;
  stroke: #ddd;
}
.circle-progress {
  fill: none;
  /* stroke: var(--color1); */
  stroke-linecap: round;
  stroke-linejoin: round;
}
.circle-text{
  font-size: 2rem;
  font-weight: bold;
}


